import React from "react";
import './Page.css';


export default class Page extends React.Component {
    constructor(props) {
        super(props);
        this.state = { n: this.props.n, pics: [] };
        this.nextPage.bind(this);
    }

    async getImgs(n) {
        let new_pics = await fetch(`https://picsum.photos/v2/list?page=${n}&limit=25`);
        new_pics = await new_pics.json();
        new_pics = new_pics.map((e) => e.id)

        return new_pics
        // this.setState({ pics: new_pics });
        // console.log(this.state.pics);
        // console.log(this.state.n);

    }

    nextPage = async ()=>{
        window.scrollTo(0, 0);
        let new_pics = await this.getImgs(this.state.n+1);
        await this.setState({
            n: this.state.n+1,
            pics: new_pics
        });
         
    }

    previousPage = async ()=>{
        window.scrollTo(0, 0);
        let new_pics = await this.getImgs(this.state.n-1);
        await this.setState({
            n: this.state.n-1,
            pics: new_pics
        });
    }


    async componentDidMount() {
        let new_pics = await this.getImgs(this.props.n);
        await this.setState({pics: new_pics});
    }

    render() {
        let pics = [];
        for (let pic of this.state.pics) {
            let url = `https://picsum.photos/id/${pic}/200/300`;
            pics.push(<img src={url}></img>)
        }
        
        if (this.state.n > 10) {
            pics.push(<button onClick={this.previousPage}>Previous</button>)
        }
        
        if (this.state.n < 100) {
            pics.push(<button onClick={this.nextPage}>Next</button>)
        }
        
        return (
            <div className="Page">{pics}</div>
        )
    }
}